function NavItem (props) {

	if (props.submenu) {
		const SubMenu = props.submenu;
		return (
			<div className="nav-item">
				<span>{props.name}</span>
				<Icon name="chevron-down" />
				<SubmenuWrap name={props.name} setGame={props.setGame}>
					<SubMenu {...props} />
				</SubmenuWrap>
			</div>
		);
	}

	return (
		<NavLink exact to={props.to}>
			<span>{props.name}</span>
		</NavLink>
	);

}

function SubmenuWrap (props) {

	return (
		<div className="submenu-wrap">
			<div className="submenu">
				<div className={`submenu--inner item--${props.name}`}>
					{props.children}
				</div>
			</div>
		</div>
	);

}

function Nav () {

	const history = useHistory();

	const { userAuth } = useContext(User.Context);

	const [form ,setForm ]= useState({
		username: '',
		password: '',
	})

	const fields = [
		{
			name: 'username',
			type: 'text',
			placeholder: '',
			value: form.username,
		},
		{
			name: 'password',
			type: 'password',
			placeholder: '',
			tick: '',
			link: '',
			value: form.password,
		}

	]

	const login = () =>{
		const req = User.login(form)
		req.promise.then(r => {
			localStorage.setItem('user', JSON.stringify(form));
			window.location.reload();

		}, e => {
			
		});
	}


	const logout = async () => {
		await User.logout();
		window.location.reload();
	}

	const onChange = e =>{
		const { name, value } = e.target;

		setForm(f => ({
			...f,
			[name]: value,
		}));
	}


	const setGame = (e, { id, name }) => {
		history.push({
			pathname: `/game/${id}/${name}`,
			search: '?from_home=1',
		});
	}

	return (
		<div className="nav">
			<div className="nav-inner">
				<div className="inline logo-wrap">
					<a href="/">App</a>
				</div>
				<div className="inline menu-wrap">
					<NavItem name="" to="/" />
					<NavItem name="" submenu={SubmenuSports} setGame={setGame} />
					<NavItem name="" submenu={SubmenuCasino} setGame={setGame} />
					<NavItem name="" submenu={SubmenuEsport} setGame={setGame} />
					<NavItem name="" submenu={SubmenuChess} setGame={setGame} />
					<NavItem name="" submenu={SubmenuSlots} setGame={setGame} />
					<NavItem name="" to="/promotions" />
					<NavItem name="APP" to="/applications" />
				</div>
				<div className="inline user-wrap">
					{userAuth.data ? (
					<>
					<div className="user-icon"/>
					<div className="user-block">
						<div className="user-links">
							<p className="user-name">{userAuth.data.account}</p>
							<div className="links">
								<Link to="/inbox"></Link>
								<Link to="/dashboard/transactions?tab=debit"></Link>
								<Link to="/dashboard/deposit?a=1"></Link>
								<Link to="/dashboard/transactions?tab=transfer"></Link>
							</div>
						</div>
						<div className="user-balance">
							<p className="user-balance-amount"><i>{userAuth.data.balance}</i> </p>
							<button className="logout-button" onClick={logout}></button>
						</div>
					</div>
					<div className="user-avatar-wrap">
						<div className="user-avatar" />
						<div className="user-avatar-overlay" />
					</div>
					</>
					) : (
					<div className="home-pre-login">
						<div className="home-pre-fields">
							{fields && map(fields , (obj ,i ) =>{
								return 	<div key={i} className="home-pre-input-wrap">
											<input {...obj} 
											onChange={onChange}
											/>
											{obj.tick && <Link to={obj.link} className="tick">{obj.tick}</Link>}
										</div>
							})}
						</div>
						<div className="home-pre-button">
							<button  onClick={login} className="button-default"></button>
							<Link to="/login?tab=signup" className="button-default secondary"></Link>
						</div>
					</div>
					)}
				</div>
			</div>
		</div>
	);

}

export default Nav;
